<template>	
	<v-row>
		<v-col 
			v-for="item in dataTraction.pertanyaan" :key="item.id"
			cols="12" md="6" sm="12">
			<v-card>
				<v-card-title class="py-2">
					{{ item.pertanyaan }}
				</v-card-title>
				<v-card-text>
					<canvas :id="`grafik-${item.id}`"></canvas>
				</v-card-text>
			</v-card>
		</v-col>
	</v-row>
</template>

<script>
	import { mapState } from 'vuex'
	import Chart from 'chart.js'

	const dataChart = {
		type: 'line',
		data: {
			labels: ['options'],
			datasets: [{
				label: ['options'],
				data: [9, 4, 8, 7, 9, 10],
				backgroundColor: ['#EF9A9A'],
				borderColor: ['#EF9A9A'],
				// borderWidth: 2
			}]
		},
		options: {
			responsive: true,
			lineTension: 1,
			scales: {
				yAxes: [{
					ticks: {
						beginAtZero: true,
						padding: 25
					}
				}]
			}
		}
	}

	export default{
		data: () => ({
			dataChart: dataChart
		}),

		mounted() {
			this.createChart(this.dataTraction.pertanyaan)
		},

		computed: {
			...mapState('dataStartupDashboard', ['dataStartup', 'dataTraction'])		
		},

		methods: {
			createChart(chartData) {
				chartData.map(x => {
					const cfx = document.getElementById(`grafik-${x.id}`)
					new Chart(cfx, {
						type: this.dataChart.type,
						data: this.dataChart.data,
						oprions: this.dataChart.options
					})
				})
			}
		}
	}
</script>