<template>
	<div class="pa-md-6 pa-sm-3">
		<v-row>
			<v-col cols="12" sm="12">
				<TabelStartup/>
			</v-col>
			<v-col cols="12" md="6" sm="12">
				<DaftarStartup/>
			</v-col>
			<v-col cols="12" md="6" sm="12">
				<DaftarTraction/>				
			</v-col>
		</v-row>

		<v-alert 
			v-if="grafikVisible" 
			type="info">
			Silahkan pilih STARTUP dan TRACTION terlebih dahulu
		</v-alert>

		<Grafik v-else />
	</div>
</template>

<script>
	import { mapState } from 'vuex'
	import DaftarStartup from '@/components/Incubator/Dashboard/DaftarStartup'
	import TabelStartup from '@/components/Incubator/Dashboard/TabelStartup'
	import DaftarTraction from '@/components/Incubator/Dashboard/DaftarTraction'
	import Grafik from '@/components/Incubator/Dashboard/Grafik'

	export default{
		components: {
			Grafik,
			DaftarTraction,
			TabelStartup,
			DaftarStartup
		},
		created() {
			this.$store.dispatch('dataIncubatorTraction/getDataTraction')
			this.$store.dispatch('dataIncubatorTraction/updateIsLoading', true)
		},
		computed: {
			...mapState('dataIncubatorDashboard', ['dataStartup', 'dataTraction']),
			grafikVisible() {
				if (this.dataStartup.length === 0 && this.dataTraction === null) {
					return true
				} else {
					return false
				}
			}
		},
	}
</script>